$Softred: hsl(10, 79%, 65%); 
$Cyan: hsl(186, 34%, 60%); 
$Darkbrown: hsl(25, 47%, 15%) ;
$Mediumbrown: hsl(28, 10%, 53%) ; 
$Cream: hsl(27, 66%, 92%);
$Verypaleorange: hsl(33, 100%, 98%);
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
}
body {
    font-family: 'DM Sans', sans-serif;
    min-height: 100vh;
    overflow: hidden;
    .card-container {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $Cream;
        width: 70%;
        margin: 60px auto 0 auto;
        height: 600px;
        .card {
            .balance {
                background-color: $Softred;
                margin: 20px 0;
                padding: 15px;
                position: relative;
                min-height: 80px;
                color: $Verypaleorange;
                width: 340px;
                border-radius: 15px;
                span {
                    font-size: 0.7em;
                }
                p {
                    font-size: 1.1em;
                    font-weight: 700;
                }
                #logo {
                    position: absolute;
                    right: 5%;
                    top: 50%;
                    transform: translateY(-50%);
                    height: 30px;
                }
            }
            .spending {
                background-color: $Verypaleorange;
                padding: 20px;
                border-radius: 15px;
                min-height: 320px;
                h2 {
                    font-size: 1.1em;
                }
                .chart {
                    margin-top: 20px;
                    width: 100%;
                    ul {
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
                    width: 100%;
                    li {
                        max-width: 35px;
                        text-align: center;
                    }
                    }
                }
              .chart-grid {    
                margin-top: 25px;
                 height: 130px;  
                 width: 100%; 
                 ul {
                    width: 100%;
                    height: 100%;
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
                    li {
                        cursor: pointer;
                    border-radius: 5px;    
                    align-self:flex-end;
                    max-width: 35px;
                    background-color: $Softred;
                    font-size: 0.8em;
                    position: relative;
                    transition: 0.2s ease;
                    &:hover {
                        opacity: 0.6;
                    }
                    span {
                        position: absolute;
                        color: $Verypaleorange;
                        background-color: $Darkbrown;
                        top: -50%;
                        padding: 2px;
                        border-radius: 4px;
                    }
                    #wen {
                            background-color: $Cyan;
                        }
                    }
                 }
           }
                .lower-card {
                    margin-top: 50px;
                    position: relative;
                    ul {
                        display: flex;
                        justify-content: space-between;
                        .left {
                            span {
                                font-size: 0.8em;
                                color: $Mediumbrown;
                            }
                            p {
                                font-size: 1.6em;
                                font-weight: 700;
                            }
                        }
                        .right {
                            margin-top: 10px;
                            text-align: right;
                           
                            span {
                                font-size: 0.8em;
                                font-weight: 700;
                            }
                            p {
                                font-size: 0.8em;
                                color: $Mediumbrown;
                            }
                        }
                    }
                    &::before {
                        position: absolute;
                        content: '';
                        width: 100%;
                        height: 0.5px;
                        top: -35%;
                        background-color: $Mediumbrown;
                        
                    }
                }
            }
        }
    }
}
